import  request  from '@/helpers/request'
import  {friendlyDate} from '@/helpers/util'

const URL = {
    GET: '/notebooks',
    ADD: '/notebooks',
    PATCH: '/notebooks/:id',
    DELETE: '/notebooks/:id'
}

export default  {
    getAll() {
        return new Promise((resolve, reject) => {
            request(URL.GET).then(res => {
                res.data = res.data.sort((notebook1, notebook2) => notebook1.createdAt < notebook2.createdAt)
                res.data.forEach(notebook => {
                    notebook.createdAtFriendly = friendlyDate(notebook.createdAt)
                    notebook.updatedAtFriendly = friendlyDate(notebook.updatedAt)
                })
                resolve(res)
            }).catch(err => {
                reject(err)
            })
        })
    },

    addNotebook({title}) {
        return new Promise((resolve, reject) => {
            request(URL.ADD, 'POST', {title}).then(res => {
                res.data.createdAtFriendly = friendlyDate(res.data.createdAt)
                res.data.updatedAtFriendly = friendlyDate(res.data.updatedAt)
                resolve(res)
            }).catch(err => {
                reject(err)
            })
        })
    },

    updateNoteBook(noteBookId, {title = ''} = {title: ''}){
        return request(URL.PATCH.replace(':id', noteBookId), 'PATCH', {title})
    },

    deleteNoteBook(noteBookId) {
        return request(URL.DELETE.replace(':id', noteBookId), 'DELETE')
    }
}


